/* Horizontal Rule Styles */
.footer-divider {
  border: 0;
  height: 1px;
  background: #ccc;
  margin: 0 20px 40px 20px;
}

/* Footer styles */
.footer {
  background-color: #e3f2fd;
  color: #333;
  padding: 40px 20px;
  font-family: "Roboto", sans-serif;
  height: 100%;
}

.footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 40px;
}

.footer-left {
  flex: 1 1 100%;
  max-width: 300px;
}

.footer-logo {
  width: 150px;
  margin-bottom: 20px;
}

.footer-description {
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 20px;
}

.footer-right {
  flex: 2 1 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: space-between;
}

.footer-column {
  flex: 1;
  min-width: 150px;
}

.footer-column h4 {
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 500;
  color: #333;
}

.footer-column ul {
  list-style: none;
  padding: 0;
  font-size: 14px;
  line-height: 2;
}

.footer-column ul li {
  margin-bottom: 5px;
}

.footer-column ul li a {
  color: #333;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-column ul li a:hover {
  color: #0083ca;
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #ccc;
  padding-top: 20px;
  margin-top: 20px;
}

.footer-bottom a {
  color: #333;
  text-decoration: none;
  margin-right: 10px;
  font-size: 14px;
}

.footer-bottom a:hover {
  color: #0083ca;
}

.footer-bottom p {
  margin: 0;
  font-size: 14px;
}

/* Media Queries */
@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    align-items: center;
  }

  .footer-right {
    flex-direction: column;
    align-items: center;
  }

  .footer-column {
    width: 100%;
    text-align: center;
  }

  .footer-bottom {
    flex-direction: column;
    align-items: center;
  }

  .footer-bottom a {
    margin: 5px 0;
  }
}
